



























import { showToast } from 'unit/index.js'
export default {
	data () {
		return{
			account: '',
			vercode: '',
			password: '',
		}
	},
	methods:{
		login() {
			this.fromRul()
		},
		fromRul() {
			if (!this.account) {
				showToast('用户名不能为空')
				return
			}
			if (!this.vercode) {
				showToast('验证码不能为空')
				return
			}
			if (!this.password) {
				showToast('密码不能为空')
				return
			}
		}
	}
}



